body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-image: url(/drum-machine/static/media/bg-cyber.ce2e6439.jpg);
  min-height: 100vh;
  display: grid;
  grid-template-areas: 
    "title title title"
    ". calculator ."
    "footer footer footer";
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  grid-template-columns: 1fr 300px 1fr;
  grid-template-rows: 250px auto auto auto auto
}
.App-link {
  color: #61dafb;
}
#title{
  grid-area: title;
  width: auto;
  min-width: 300px;
  padding: 25px;
}
#calculator{
  grid-area: calculator;
  width: 100%;
}
#footer{
  grid-area: footer;
}


#calculator{
    display: grid;
    grid-template-columns: repeat(4, 75px [col-start]);
    grid-template-rows: 30px repeat(5, 65px [row-start]) auto;
    grid-template-areas: 
        'formula formula formula formula'
        'display display display display'
        'seven eight nine add'
        'four five six subtract'
        'one two three multiply'
        '. zero deci divide'
        'clear equals equals equals';
}
#formula-display{
    grid-area: formula;
    width: 100%;
    text-align: right;
    font-size: 1em;
    padding: 2px 10px;
}
#display{
    grid-area: display;
    width: 100%;
    height: 100%;
    font-size: 2.2em;
    align-self: end;
    justify-self: end;
    text-align: right;
}
#add{
    grid-area:add;
}
#subtract{
    grid-area:subtract;
}
#multiply{
    grid-area:multiply;
}
#divide{
    grid-area:divide;
}
#seven{
    grid-area:seven;
}
#eight{
    grid-area:eight;
}
#nine{
    grid-area:nine;
}
#four{
    grid-area:four;
}
#five{
    grid-area:five;
}
#six{
    grid-area:six;
}
#one{
    grid-area:one;
}
#two{
    grid-area:two;
}
#three{
    grid-area:three;
}
#zero{
    grid-area:zero;
}
#decimal{
    grid-area: deci;
}
#equals-container{
    grid-area:equals;
    width: 100%;
}
#clear-container{
    grid-area:clear;
    width: 100%;
}
